@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: absolute;
    bottom: 20vh;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.logo {
    text-align: center;
    color: white;
    margin: 2vh 0;
    font-size: 1.3em;
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
}

.logoIcon {
    display: -webkit-flex;
    display: flex;
    margin: 12vh auto 2vh;
    width: 25vh;
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
}

@media screen and (min-width: 580px) {
    .logoIcon {
        width: 30vh;
    }

    .logo {
        font-size: 1.5em;
    }
}

input {
    display: block;
    background: transparent;
    border: 0;
    border-bottom: 1px solid rgba(255, 255, 255, .4);
    padding: 12px 20px;
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
    text-align: center;
}

input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 1);
    font-family: 'Montserrat', sans-serif;
    font-size: 1.1em;
}

input::-ms-input-placeholder {
    color: rgba(255, 255, 255, 1);
    font-family: 'Montserrat', sans-serif;
    font-size: 1.1em;
}

input::placeholder {
    color: rgba(255, 255, 255, 1);
    font-family: 'Montserrat', sans-serif;
    font-size: 1.1em;
}

input[value] {
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.1em;
}

input:focus {
    outline: none;
    box-shadow: 0 0;
    border-bottom: 1px solid rgba(255, 255, 255, 1);
}
/* SUNNY */
.sunny {
    -webkit-animation: sunny 15s linear infinite;
            animation: sunny 15s linear infinite;
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0) 100%);
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0) 100%);
    height: 140px;
    width: 20px;
    position: absolute;
    left: 15vw;
    top: 20px;
}

.sunny:before {
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0) 100%);
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0) 100%);
    content: '';
    height: 140px;
    width: 20px;
    opacity: 1;
    position: absolute;
    bottom: 0px;
    left: 0px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.sunny:after {
    background: #FFEE44;
    border-radius: 50%;
    box-shadow: rgba(255, 255, 0, 0.2) 0 0 0 15px;
    content: '';
    height: 80px;
    width: 80px;
    position: absolute;
    left: -30px;
    top: 30px;
}

@-webkit-keyframes sunny {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes sunny {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

/* CLOUDY */
.cloudy {
    -webkit-animation: cloudy 5s ease-in-out infinite;
            animation: cloudy 5s ease-in-out infinite;
    background: #FFFFFF;
    border-radius: 50%;
