#fabrizio {
    border-top: 100px solid black;
    border-right: 63px solid transparent;
    width: 0;
    -webkit-transform: scale(.33);
            transform: scale(.33);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    margin: 20px;
    opacity: .6
}

#fabrizio:before {
    background: black;
    width: 20px;
    height: 30px;
    content: '';
    display: block;
    -webkit-transform: skewX(-32deg);
            transform: skewX(-32deg);
    position: relative;
    top: -64px;
    left: 30px;
}

#fabrizio:after {
    background: black;
    width: 40px;
    height: 36px;
    content: '';
    display: block;
    -webkit-transform: skewX(-32deg);
            transform: skewX(-32deg);
    position: relative;
    top: -130px;
    left: 50px;
}
.weatherInfoContainer {
    color: white;
    display: block;
    max-width: 100%;
    width: 600px;
    padding: 10px 30px;
}

@media screen and (min-width: 580px) {
    .weatherInfoContainer {
        margin: 0 auto;
    }
}

.weatherInfoContainer h4 {
    font-family: 'Montserrat';
    font-weight: 400;
    margin: 20px 0px;
}

.weatherInfoContainer h2 {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 2em;
    margin: 0px;
}

.weatherInfoCity {
    border-bottom: 2px solid rgba(255, 255, 255, .3);
    text-align: center;
    padding: 30px 0;
}

.weatherInfoCity h4 {
    margin: 7px;
}

.weatherInfoDetails {
    margin-top: 4vh;
    text-align: center;
}

.weatherInfoDetails h2 {
    font-size: 3em;
    margin: 0;
    font-weight: 400;
}

.weatherInfoDetails div {
    margin-top: 30px;
}

.weatherInfoDetails h5 {
    font-size: .9em;
    margin: 0;
    font-weight: 400;
}

.error {
    color: white;
    width: 100%;
    text-align: center;
    position: absolute;
    top: 34%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-weight: 400;
    font-size: 1.8em;
}
footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 0px 20px;
    color: black;
    text-align: center;
}

footer p {
    font-size: .9em;
}

footer a {
    color: #2980b9;
    text-decoration: none;
}

footer span {
    color: #c0392b;
    font-size: 1.8em;
}
